import React from 'react';
import { Link } from 'gatsby';
import { css } from '@emotion/core';
import { palettes } from 'src/styles/constants/theme'

const classes = {
  list: css({
    width: '100%',
    display: 'flex',
    position: 'relative',
    boxSizing: 'border-box',
    padding: '8px 16px',
    textAlign: 'left',
    alignItems: 'center',
    verticalAlign: 'middle',
    justifyContent: 'flex-start',
    textDecoration: 'none',
    backgroundColor: 'transparent',
    cursor: 'pointer',
    transition: 'background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
    '&:hover': {
      textDecoration: 'none',
      backgroundColor: palettes.action.hover,
    },
  }),
  listIcon: css({
    display: 'inline-flex',
    minWidth: '56px',
    color: palettes.text.secondary,
    fontSize: '3rem',
    flexShrink: 0,
  }),
  listText: css({
    flex: '1 1 auto',
    minWidth: 0,
    marginTop: '4px',
    marginBottom: '4px',
  }),
}

interface ListsProps {
    icon: JSX.Element,
    text: string,
    endIcon?: JSX.Element,
    link?: string,
}

/**
 * @param {JSX.Element} icon svg element
 * @param {string} text list title
 * @param {JSX.Element?} endIcon? svg element
 * @returns {JSX.Element} List element
 */
const Component: React.FCX = (({className}, props: ListsProps) => {
  let wrapper;
  if(props.link){
    wrapper = <>
                <div className={className}>
                <Link to={props.link} css={classes.list}>
                    <div css={classes.listIcon}>{props.icon}</div>
                    <div css={classes.listText}>{props.text}</div>
                    {props.endIcon && props.endIcon}
                </Link>
                </div>
              </>;
  } else {
    wrapper = <>
                <div className={className}>
                    <div css={classes.listIcon}>{props.icon}</div>
                    <div css={classes.listText}>{props.text}</div>
                    {props.endIcon && props.endIcon}
                </div>
              </>;
  }

  return (
    <>
      {wrapper}
    </>
  )
});

export default Component;