import React, { useState } from 'react';
import { SwordIcon } from 'src/components/icons'
import { Link } from 'gatsby';
import { NestedLists } from 'src/components';
import { css } from '@emotion/core';
import { palettes } from 'src/styles/constants/theme'

const classes = {
  root: css({
    top: 0,
    left: 0,
    right: 'auto',
    display: 'flex',
    flex: '1 0 auto',
    flexDirection: 'column',
    width: '240px',
    height: '100%',
    outline: 0,
    zIndex: 1200,
    position: 'fixed',
    overflowY: 'auto',
    borderRight: `1px solid ${palettes.divider}`,
    backgroundColor: palettes.background.paper,
  }),
  list: css({
    width: '100%',
    display: 'flex',
    position: 'relative',
    boxSizing: 'border-box',
    padding: '8px 16px',
    textAlign: 'left',
    alignItems: 'center',
    verticalAlign: 'middle',
    justifyContent: 'flex-start',
    textDecoration: 'none',
    backgroundColor: 'transparent',
    cursor: 'pointer',
    transition: 'background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
    '&:hover': {
      textDecoration: 'none',
      backgroundColor: palettes.action.hover,
    },
  }),
  listIcon: css({
    display: 'inline-flex',
    minWidth: '56px',
    color: palettes.text.secondary,
    fontSize: '3rem',
    flexShrink: 0,
  }),
  listText: css({
    flex: '1 1 auto',
    minWidth: 0,
    marginTop: '4px',
    marginBottom: '4px',
  }),
  listHeader: css({
    top: 0,
    zIndex: 1,
    position: 'sticky',
    fontFamily: "Segoe UI",
    color: palettes.text.secondary,
    fontSize: '2rem',
    boxSizing: 'border-box',
    listStyle: 'none',
    fontWeight: 500,
    lineHeight: '48px',
    paddingLeft: '16px',
    paddingRight: '16px',
  }),
}

const Component: React.FCX = ({ className }) => (
    <nav className={className} css={classes.root}>
        <div css={classes.listHeader}>Calculators</div>
        <NestedLists
            topIcon={<SwordIcon />}
            topText={"ダメージ計算"}
            lists={[{icon: <SwordIcon />, text: "コロシアム", link: "/"},
                    {icon: <SwordIcon />, text: "モノガタリ", link: "/"}]}
        />
        <NestedLists
            topIcon={<SwordIcon />}
            topText={"バフデバフ計算"}
            lists={[{icon: <SwordIcon />, text: "コロシアム", link: "/"},
                    {icon: <SwordIcon />, text: "モノガタリ", link: "/"}]}
        />
        <NestedLists
            topIcon={<SwordIcon />}
            topText={"回復計算"}
            lists={[{icon: <SwordIcon />, text: "コロシアム", link: "/"},
                    {icon: <SwordIcon />, text: "モノガタリ", link: "/"}]}
        />
        <div css={classes.list}></div>
    </nav>
);

