import React, { useMemo, useState } from 'react';
import { ChevronUp, ChevronDown } from 'src/components/icons'
import { Link } from 'gatsby';
import { css } from '@emotion/core';
import { palettes } from 'src/styles/constants/theme'

const classes = {
  nestedWrapper: css({
    display: 'flex',
    flexDirection: 'column',
    minHeight: 0,
    transition: 'all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
    transitionDuration: '235ms',
    overflow: 'hidden',
  }),
  list: css({
    width: '100%',
    display: 'flex',
    position: 'relative',
    boxSizing: 'border-box',
    padding: '8px 16px',
    textAlign: 'left',
    alignItems: 'center',
    verticalAlign: 'middle',
    justifyContent: 'flex-start',
    textDecoration: 'none',
    backgroundColor: 'transparent',
    cursor: 'pointer',
    transition: 'background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
    '&:hover': {
      textDecoration: 'none',
      backgroundColor: palettes.action.hover,
    },
  }),
  nestedList: css({
    paddingLeft: '32px',
  }),
  listIcon: css({
    display: 'inline-flex',
    minWidth: '56px',
    color: palettes.text.secondary,
    fontSize: '3rem',
    flexShrink: 0,
  }),
  listText: css({
    flex: '1 1 auto',
    minWidth: 0,
    marginTop: '4px',
    marginBottom: '4px',
  }),
}

interface ChildListsProps {
  lists: {icon: JSX.Element, text: string, link?: string}[]
}

interface NestedListsProps {
    className?: string,
    topIcon: JSX.Element,
    topText: string,
    lists: {icon: JSX.Element, text: string, link?: string}[]
}

const ChildLists = React.memo((props: ChildListsProps) => {
  return(
    <>
      {props.lists.map((list, key) => (
        <div key={key}>
          <Link to={list.link} css={[classes.list, classes.nestedList]}>
            <div css={classes.listIcon}>{list.icon}</div>
            <div css={classes.listText}>{list.text}</div>
          </Link>
        </div>
      ))}
    </>
  )
});

/**
 * @param {JSX.Element} topIcon svg element
 * @param {string} topText list title
 * @param {{icon: JSX.Element, text: string, link?: string}[]} lists Nested list arrays
 * @returns {JSX.Element} Nested list elements
 */
const Component = React.memo((props: NestedListsProps) => {
  const [open, setOpen] = useState(false);
  const handleClick = () => {
    setOpen(!open);
  };
  const nodeStyle = useMemo(
    () => ({
      maxHeight: open ? `${props.lists.length * 50}px` : 0,
    }), [open]
  )

  return (
    <>
      <div className={props.className}>
        <div css={classes.list} onClick={handleClick}>
          <div css={classes.listIcon}>{props.topIcon}</div>
          <div css={classes.listText}>{props.topText}</div>
          {open ? <ChevronUp /> : <ChevronDown />}
        </div>

        <div style={nodeStyle} css={classes.nestedWrapper}>
          <ChildLists lists={props.lists} />
        </div>
      </div>
    </>
  )
});

export default Component;